<template>
  <div class="app-main">
    <div class="screening-wrap">
      <el-select v-model="activeSite" placeholder="请选择" style="display: inline-block;width: 120px;" @change="handleChangeSite">
        <el-option
            v-for="item in sourceSite"
            :key="item.value"
            :label="item.label"
            :value="item.value">
        </el-option>
      </el-select>
      <div class="tabs-wrap">
        <el-radio-group v-model="activeTab" size="mini" @change="handleChangeTab">
          <el-radio-button :label="tab.value" v-for="(tab,index) in tabs" :key="index" style="border: none;">{{ tab.label }}</el-radio-button>
        </el-radio-group>
      </div>
      <div class="refresh-btn" @click="handleRefresh">
        <i class="el-icon-refresh"></i>
      </div>
    </div>
    <div class="movies-wrap" v-loading.fullscreen.lock="fullscreenLoading">
      <MoviesWrap :moviesData="moviesData" :category="category" :site="activeSite" />
    </div>
    <div class="prev-next-btn">
      <el-button icon="el-icon-back" type="success" @click="handlePrev" style="margin-right: 10px;" :disabled="pageNum === 1" circle></el-button>
      <el-button icon="el-icon-right" type="danger" @click="handleNext" circle></el-button>
    </div>
  </div>
</template>

<script>
import MoviesWrap from "./MoviesWrap"
import { siteInfo,sourceSite } from '../store'
import { btdx,meijutt,rijutv } from "../crawler"

const cheerio = require('cheerio')
const iconv = require('iconv-lite')
export default {
  name: "AppMain",
  components: {
    MoviesWrap,
  },
  data(){
    return {
      moviesData: [],
      activeSite: 'meijutt',
      pageNum: 1,
      sourceSite: sourceSite,
      tabs: siteInfo['meijutt']['tabs'],
      activeTab: '2021',
      fullscreenLoading: false,
      category: '',
    }
  },
  methods: {
    async getBTDX(){
      let baseHost = ''
      if (process.env.NODE_ENV === "development") {
        baseHost = '/btdx/'
      } else {
        baseHost = 'https://www.btdx8.com/category/'
      }
      this.fullscreenLoading = true
      let url = baseHost + this.activeTab
      if (this.pageNum === 2){
        url = baseHost + this.activeTab  + '/page/' + this.pageNum
      }
      this.moviesData = await btdx.fetchList(url)
      this.fullscreenLoading = false
    },
    async getMeiJuTT(){
      let baseHost = ''
      if (process.env.NODE_ENV === "development") {
        baseHost = '/meijutt/'
      }else {
        baseHost = 'https://www.meijutt.tv/'
      }
      this.fullscreenLoading = true
      let url = baseHost + this.pageNum + '__' + this.activeTab + '_____.html'
      this.moviesData = await meijutt.fetchList(url)
      this.fullscreenLoading = false
    },
    async getRiJuTV(){
      let baseHost = ''
      if (process.env.NODE_ENV === "development") {
        baseHost = '/rijutv/'
      }else {
        baseHost = 'https://www.rijutv.com/'
      }
      this.fullscreenLoading = true
      let url = baseHost + 'v_all/list-catid-7-year-' + this.activeTab + '-page-' + this.pageNum + '.html'
      this.moviesData = await rijutv.fetchList(url)
      this.fullscreenLoading = false
      if(this.moviesData.length > 0){
        for (const key in this.moviesData) {
          let home_url = this.moviesData[key]['home_url']
          if (process.env.NODE_ENV === "development") {
            home_url =  home_url.replace('https://www.rijutv.com/','/rijutv/')
          }
          let detailed = await rijutv.fetchDetailed(home_url)
          this.$set(this.moviesData[key], 'alias_name', detailed['alias_name'])
          this.$set(this.moviesData[key], 'premiere_date', detailed['premiere_date'])
          this.$set(this.moviesData[key], 'episode', detailed['episode'])
          this.$set(this.moviesData[key], 'introduction', detailed['introduction'])
        }
      }
    },
    getMoviesData(){
      if(this.activeSite === 'btdx'){
        this.category = '电影'
        this.getBTDX()
      } else if (this.activeSite === 'meijutt'){
        this.category = '美剧'
        this.getMeiJuTT()
      } else if(this.activeSite === 'rijutv'){
        this.category = '日剧'
        this.getRiJuTV()
      }
    },
    handleChangeTab(val){
      this.activeTab = val
      this.pageNum = 1
      this.getMoviesData()
    },
    handleChangeSite(val){
      if(siteInfo.hasOwnProperty(val)){
        this.tabs = siteInfo[val]['tabs']
        this.activeTab = siteInfo[val]['tabs'][0]['value']
        this.pageNum = 1
        this.getMoviesData()
      }
    },
    handlePrev(){
      this.pageNum -= 1
      this.getMoviesData()
    },
    handleNext(){
      this.pageNum += 1
      this.getMoviesData()
    },
    handleRefresh(){
      this.getMoviesData()
    }
  },
  mounted() {

  }
}
</script>

