<template>
  <div>
    <el-row :gutter="10" v-if="moviesData && moviesData.length > 0" style="width: calc(100vw - 340px);">
      <el-col :span="8" :xs="24" :sm="24" :md="12" :lg="12" :xl="8" v-for="(movie,index) in moviesData" :key="index">
          <div class="movie-item">
            <el-row :gutter="20">
              <el-col :span="6">
                <a :href="movie.home_url" class="home-btn" target="_blank">
                  <div class="movie-item-poster">
                    <img :src="movie.poster" alt="" class="poster">
                  </div>
                </a>
              </el-col>
              <el-col :span="18">
                <div class="movie-item-introduction">
                  <div class="movie-title">
                    {{ movie.title }}
                    <el-tooltip class="item" effect="dark" content="点击添加到追剧" placement="right" v-if="site !== 'btdx'">
                      <i class="el-icon-alarm-clock follow-btn" @click="handleFollow(movie.title,movie.home_url,movie.episode)"></i>
                    </el-tooltip>
                  </div>
                  <a :href="movie.home_url" target="_blank">
                    <div class="movie-intro-item" v-if="movie.last_date && movie.last_date !== ''">{{ movie.last_date }}</div>
                    <div class="movie-intro-item" v-if="movie.premiere_date && movie.premiere_date !== '' ">{{ movie.premiere_date }}</div>
                    <div class="movie-intro-item" v-if="movie.episode && movie.episode !== '' ">{{ movie.episode }}</div>
                    <div class="movie-intro-item" v-if="movie.category && movie.category !== '' ">{{ movie.category }}</div>
                    <div class="movie-introduction" v-if="movie.introduction && movie.introduction !== ''">{{ movie.introduction }}</div>
                  </a>
                </div>
              </el-col>
            </el-row>
          </div>

      </el-col>
    </el-row>
  </div>
</template>

<script>
import Bus from '../bus'
export default {
  name: "MoviesWrap",
  components: {

  },
  props: {
    moviesData: {
      type: Array,
      default: function(){
        return []
      }
    },
    category: {
      type: String,
      default: ''
    },
    site: {
      type: String,
      default: ''
    }
  },
  data(){
    return {

    }
  },
  methods: {
    handleFollow (title, home_url,episode) {
      console.log(title)
      console.log(episode)
      let episodeNum = episode.search(/\d/) > -1 ? parseInt(episode[episode.search(/\d/)]) : 0
      console.log(episodeNum)
      let data = {
        'title': title,
        'home_url': home_url,
        'category': this.category,
        'episode': episodeNum
      }
      let follow_data = localStorage.getItem('follow_data')
      if(follow_data == null){
        follow_data = []
      } else {
        follow_data = JSON.parse(follow_data)
        let flag = follow_data.some((item) => {
          if(item['title'] === title && item['home_url'] === home_url){
            return true
          }
        })
        if(flag){
          this.$message.warning(title + '已在追剧列表中，请勿重复添加~')
          return
        }
      }
      follow_data.push(data)
      localStorage.setItem('follow_data',JSON.stringify(follow_data))
      Bus.$emit('addFollow', data)
      this.$message.success(title + ' 已加入追剧列表~')
    },

  },

}
</script>

